import { request } from 'utils';

const prefix = '/api/categories';

export const getYearEducations = ({ page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: '/YearEducation',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });



