import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Card, Form, Input, Select } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getYearEducations } from 'services/yearEducationForEnrollmentPlan';
import { getCourses } from 'services/courseForEnrollmentPlan';
import { getAreas } from 'services/areaForEnrollmentPlan';
import { requiredRule } from 'utils/validateRules';
import { getQueryParams } from 'utils/urlUtil';


import {
  getEnrollSolutionsById,
  createEnrollSolutions,
  updateEnrollSolutions,
} from 'services/enrollment';

@Form.create({ name: 'FormEnrollmentPlan' })
class FormEnrollmentPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      enrollSolutionsId: false,
      initialValue: {
        id: '',
        label: '',
        courseId: '',
        areaids: '',
        areas: '',
        minTotalPoint: '',
        minPointPerSubject: '',
        noOfStudent: '',
        reviewTypeIds: [],
        yearEduId: '',
      },
    };
  }

  componentDidMount = () => {
    this.handleGetParams();
  };

  getEnrollSolutionsById = async id => {
    this.setState({ enrollSolutionsId: id });
    const res = await getEnrollSolutionsById({ id });
    const initialValue = { ...this.state.initialValue, ...res.data };
    const { setFieldsValue } = this.props.form;
    if (initialValue.reviewTypeIds) {
      setFieldsValue({ reviewTypeIds: initialValue.reviewTypeIds });
    }
    this.setState({ initialValue });
  };

  handleGetParams = () => {
    const idSolutionsParams = getQueryParams().id;
    if (idSolutionsParams) {
      this.getEnrollSolutionsById(idSolutionsParams);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { enrollSolutionsId } = this.state;
    try {
      if (!enrollSolutionsId) {
        await createEnrollSolutions({
          id: values.id,
          label: values.label,
          courseId: values.courseId,
          areaids: values.areaids.join(),
          minTotalPoint: values.minTotalPoint,
          minPointPerSubject: values.minPointPerSubject,
          noOfStudent: values.noOfStudent,
          reviewTypeIds: values.reviewTypeIds.join(),
          yearEduId: values.yearEduId,
        });
      } else {
        await updateEnrollSolutions({
          id: values.id,
          label: values.label,
          courseId: values.courseId,
          areaids: values.areaids.join(),
          minTotalPoint: values.minTotalPoint,
          minPointPerSubject: values.minPointPerSubject,
          noOfStudent: values.noOfStudent,
          reviewTypeIds: values.reviewTypeIds.join(),
          yearEduId: values.yearEduId,
        });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="">
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item {...formItemLayout} label="M phng n" hasFeedback>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [requiredRule],
              })(<Input disabled={!!getQueryParams().id} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tn phng n" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khu vc" hasFeedback>
              {getFieldDecorator('areaids', {
                initialValue: initialValue.areaIds,
                rules: [requiredRule],
              })(<SelectAjax service={getAreas} mode="multiple" />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Xt theo" hasFeedback>
              {getFieldDecorator('reviewTypeIds', {
                // initialValue: initialValue.reviewTypeIds.length ? initialValue.reviewTypeIds : '1',
                rules: [requiredRule],
              })(
                <Select mode="multiple">
                  <Option id="1" key="1" name="1" value="1">
                    Hc b
                  </Option>
                  <Option id="2" key="2" name="2" value="2">
                    Thi THPT
                  </Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Kha hc" hasFeedback>
              {getFieldDecorator('courseId', {
                initialValue: initialValue.courseId,
                rules: [requiredRule],
              })(<SelectAjax service={getCourses} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Nm hc" hasFeedback>
              {getFieldDecorator('yearEduId', {
                initialValue: initialValue.yearEduId,
                rules: [requiredRule],
              })(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="im tng" hasFeedback>
              {getFieldDecorator('minTotalPoint', {
                initialValue: initialValue.minTotalPoint,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="im chi tit" hasFeedback>
              {getFieldDecorator('minPointPerSubject', {
                initialValue: initialValue.minPointPerSubject,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="S lng tuyn sinh" hasFeedback>
              {getFieldDecorator('noOfStudent', {
                initialValue: initialValue.noOfStudent,
                rules: [requiredRule],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormEnrollmentPlan {...props} />
  </Page>
));



